<template>
 <div class=" container  bg-dark text-light pb-4">
    <h1 >Yazı Yarışması</h1>
    <p>Parmakları test et bakalım.</p>

    <hr class="my-4">
     <div v-if="bitis" class=" alert alert-warning">
       <center> <h1 class="text-dark fw-bold">Süre Bitti</h1></center>
        <h3 class="fw-bolder">DKS={{dks}}</h3>
        <p class="mr-2 fw-bolder"> Doğruluk Yüzdesi=%{{dogrulukYuzdesi.toFixed()}}</p>
        <p class="mr-2 fw-bolder fw-bolder">Doğru Sayisi={{dogruSayisi}}</p>
        <p class="mr-2 fw-bolder">Yanliş Sayisi={{yanlisSayisi}}</p>

       <center> <button @click="yeniOyun" class="btn btn-dark btn-lg">Yeni Oyun</button></center>
     </div>
    <div v-else>
        <div class="card mb-3">
            <div class="card-body  text-dark">
                <span  v-for="(kelime,key) in kelimeler.filter((data,index)=>index<15)" :key="key" v-bind:class="key!=0 || yazılanKelimeKontrol "  class="ml-2 fs-3  slick-item  ">{{kelime}} ,</span>
            </div>
        </div>

        <div class="card mb-4">
            <div class="card-body text-dark">
                  <div class="input-group input-group-lg">
                      <input type="text" class="form-control" v-model="girilen_kelime">
                      <button class="btn btn-outline-secondary text-dark" type="button" disabled>{{zaman}} sn</button>
                      <button :disabled='basladi_mi' class="btn btn-outline-secondary" type="button" @click="kelimeGetir">Yenile</button>
                  </div>
            </div>
        </div>
    </div>

</div>
</template>

<script>

import kelimeListesi from '@/assets/words.json'
export default {
  data () {
    return {
      kelimeler: [],
      girilen_kelime: null,
      isTrue: true,
      dogruSayisi: 0,
      yanlisSayisi: 0,
      zaman: 60,
      zaman_degisimi: false,
      basladi_mi: false,
      bitis: false,
      kelimeListesi: kelimeListesi
    }
  },
  watch: {
    girilen_kelime (val) {
      if (!val || val === ' ') { // bosluk değeri gelince oyunu başlatmaması için yazdık
        this.girilen_kelime = ''
        return
      }
      if (!this.basladi_mi) { // zamanlayiiciyi 1 sefer çalıştırmak içn yazdık yoksa her kelime yazışımızda  sayıcı hızlanıyor
        this.zamanlayici() // bir tusa basılınca zamanlayıcıyı çalıştır demek
      }

      const kelime = this.kelimeler[0].slice(0, val.length) // burada kelimelerin elemını this methoduyla alıyoruz slice gelen veriyi kesmemizi sağlıyor elma ise  val gelen uzunluk kadar mesle val 2  el bize donduruyor  1 olda e 3 olsa elm gibi
      const kullaniciCeabi = val.replace(' ', '').toUpperCase() // bosluk olan yerlerdeki değerleri değiştirerek bosluğu yk ettik
      // kelimeyi tam yazana kadar yanlış tam yazıldığında ise doğru yazacak ekrana
      this.isTrue = kelime === kullaniciCeabi // eğer bunlar eşitse doğru yanlışşsa  false olacak tek satırda kontrolu sağlıyoruz

      if (val.indexOf(' ') !== -1) { // bosluğa basıldı mı diye kontrol ediyoruz
        this.isTrue ? this.dogruSayisi++ : this.yanlisSayisi++
        this.kelimeler.splice(0, 1) // gecmiş olduğumuz kelimeyi ekrandan kaldırıyoruz
        this.girilen_kelime = '' // input clear
        this.isTrue = true
      }
    }
  },
  computed: { // kelimenin yanlış veya doğru olduğunda  arkaplan  rengini değiştirmek için kontroller yazdık burad eğer true ise  isTrue rank aynı kalacak ama false ise kırmızı olarak döndürecek
    yazılanKelimeKontrol () {
      return this.isTrue ? 'yazılan-kelime' : 'yazılan-kelime bg-danger' //   isTrue false arka planı kırmızıya çeviren kontrolcu
    },
    dks () { // dakikadaki kelime sayisi
      return 300 - this.kelimeler.length
    },
    dogrulukYuzdesi () {
      const dogruluk = (100 / this.dks)
      const değer = (dogruluk * this.dogruSayisi)
      return isNaN(değer) ? 0 : değer
    }
  },
  mounted () { // açılr açılmaz kelime getirme fonksiyonunu çalıştrımak için mounted fonksiyonunu yazdık
    this.kelimeGetir()
  },
  methods: {
    kelimeGetir () {
      this.kelimeler = this.kelimeListesi.sort(() => Math.random() - 0.5).splice(0, 300)// random kelime getirmemizi sağlayan random kodu ve splice ile 0 ile 300 arasında kelime getiriyoruz
    },
    zamanlayici () {
      this.basladi_mi = true
      this.zaman_degisimi = setInterval(this.timeProcess, 1000)
    },
    timeProcess () {
      if (this.zaman === 0) { // sayac eksiye gitmemsi için bu kontrolü yapıyoruz
        clearInterval(this.basladi_mi)
        this.bitis = true // bitis değerini true yapıyoruz
        return
      }
      this.zaman--
    },
    yeniOyun () {
      this.kelimeGetir()
      this.bitis = false
      this.zaman = 60
      this.basladi_mi = false
    }
  }

}
</script>

<style>
    .yazılan-kelime{
      background-color: rgb(128, 128, 128);
      padding: 5px;
      border-radius: 4px;
    }
    .slick-item {
      margin: 0 5px;

      aspect-ratio: 1.3/1;
      max-width: 280px;
    }

</style>
